import React, { useState } from 'react';
import { Box, Button, TextField, Container, Paper, Typography } from '@mui/material';
import axios from 'axios';

const AdminPanel = () => {
  const [formData, setFormData] = useState({
    recordNo: '',
    text: '',
    issuedTo: '',
    certificateImage: null
  });
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleFileChange = (e) => {
    setFormData({
      ...formData,
      certificateImage: e.target.files[0]
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);
    setSuccess(false);

    const data = new FormData();
    data.append('recordNo', formData.recordNo);
    data.append('text', formData.text);
    data.append('issuedTo', formData.issuedTo);
    data.append('certificateImage', formData.certificateImage);

    try {
      await axios.post('http://localhost:5000/api/certificates', data, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      setSuccess(true);
      setFormData({
        recordNo: '',
        text: '',
        issuedTo: '',
        certificateImage: null
      });
    } catch (err) {
      setError(err.response?.data?.message || 'Error creating certificate');
    }
  };

  return (
    <Container maxWidth="md">
      <Paper elevation={3} sx={{ p: 4, mt: 4 }}>
        <Typography variant="h4" gutterBottom>
          Create Certificate
        </Typography>
        
        <Box component="form" onSubmit={handleSubmit} sx={{ mt: 3 }}>
          <TextField
            fullWidth
            label="Record Number"
            name="recordNo"
            value={formData.recordNo}
            onChange={handleInputChange}
            margin="normal"
            required
          />
          
          <TextField
            fullWidth
            label="Issued To"
            name="issuedTo"
            value={formData.issuedTo}
            onChange={handleInputChange}
            margin="normal"
            required
          />

          <TextField
            fullWidth
            label="Certificate Text"
            name="text"
            value={formData.text}
            onChange={handleInputChange}
            margin="normal"
            multiline
            rows={4}
            required
          />

          <Box sx={{ mt: 2, mb: 3 }}>
            <input
              accept="image/*"
              style={{ display: 'none' }}
              id="certificate-image"
              type="file"
              onChange={handleFileChange}
            />
            <label htmlFor="certificate-image">
              <Button variant="contained" component="span">
                Upload Certificate Image
              </Button>
            </label>
            {formData.certificateImage && (
              <Typography variant="body2" sx={{ mt: 1 }}>
                Selected file: {formData.certificateImage.name}
              </Typography>
            )}
          </Box>

          {error && (
            <Typography color="error" sx={{ mt: 2 }}>
              {error}
            </Typography>
          )}

          {success && (
            <Typography color="success.main" sx={{ mt: 2 }}>
              Certificate created successfully!
            </Typography>
          )}

          <Button
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            sx={{ mt: 3 }}
          >
            Create Certificate
          </Button>
        </Box>
      </Paper>
    </Container>
  );
};

export default AdminPanel;
