import React, { useState } from 'react';
import { Container, Paper, Typography, TextField, Button, Box, Link } from '@mui/material';
import { useNavigate, useLocation } from 'react-router-dom';
import { auth } from '../services/api';

const LoginPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const recordData = location.state?.recordData;

  const [email, setEmail] = useState('');
  const [otp, setOtp] = useState('');
  const [otpSent, setOtpSent] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSendOTP = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      await auth.sendOTP(email);
      setOtpSent(true);
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to send OTP');
    } finally {
      setLoading(false);
    }
  };

  const handleVerifyOTP = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const response = await auth.verifyOTP(email, otp);
      
      // Store the token
      localStorage.setItem('token', response.data.token);
      
      // Navigate back to home with user data
      navigate('/', { 
        state: { 
          isSignedUp: true, 
          recordData,
          user: response.data.data.user
        } 
      });
    } catch (err) {
      setError(err.response?.data?.message || 'Invalid OTP');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="sm" sx={{
      minHeight: '100vh',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: 'linear-gradient(135deg, #003366 0%, #001a33 100%)'
    }}>
      <Paper elevation={3} sx={{
        p: 4,
        width: '100%',
        borderRadius: 2,
        background: '#fff',
        border: '2px solid #c19a49'
      }}>
        <Typography variant="h4" gutterBottom sx={{
          color: '#003366',
          textAlign: 'center',
          mb: 4,
          fontWeight: 'bold'
        }}>
          Login
        </Typography>

        <form onSubmit={otpSent ? handleVerifyOTP : handleSendOTP}>
          <TextField
            fullWidth
            label="Email"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            disabled={otpSent}
            required
            sx={{ 
              mb: 3,
              '& .MuiOutlinedInput-root': {
                '&:hover fieldset': {
                  borderColor: '#c19a49',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#003366',
                }
              }
            }}
          />

          {otpSent && (
            <TextField
              fullWidth
              label="Enter OTP"
              value={otp}
              onChange={(e) => setOtp(e.target.value)}
              required
              sx={{ 
                mb: 3,
                '& .MuiOutlinedInput-root': {
                  '&:hover fieldset': {
                    borderColor: '#c19a49',
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: '#003366',
                  }
                }
              }}
            />
          )}

          {error && (
            <Typography color="error" sx={{ mt: 2, textAlign: 'center' }}>
              {error}
            </Typography>
          )}

          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, alignItems: 'center' }}>
            <Button
              type="submit"
              variant="contained"
              fullWidth
              disabled={loading}
              sx={{
                bgcolor: '#003366',
                '&:hover': {
                  bgcolor: '#004d99'
                }
              }}
            >
              {loading ? 'Processing...' : otpSent ? 'Verify OTP' : 'Send OTP'}
            </Button>

            <Typography variant="body2" sx={{ mt: 2 }}>
              Don't have an account?{' '}
              <Link
                component="button"
                variant="body2"
                onClick={() => navigate('/signup', { state: { recordData } })}
                sx={{ color: '#c19a49' }}
              >
                Sign Up
              </Link>
            </Typography>
          </Box>
        </form>
      </Paper>
    </Container>
  );
};

export default LoginPage;
