import React, { useState } from 'react';
import { Box, Button, Container, Paper, TextField, Typography, Link } from '@mui/material';
import { useNavigate, useLocation } from 'react-router-dom';
import { auth } from '../services/api';

const SignupPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const recordData = location.state?.recordData;

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      // Sign up the user
      const response = await auth.signup(formData);
      
      // Store the token
      localStorage.setItem('token', response.data.token);
      
      // Navigate back to home with the record data and signed up status
      navigate('/', { 
        state: { 
          isSignedUp: true, 
          recordData,
          user: response.data.data.user
        } 
      });
    } catch (err) {
      setError(err.response?.data?.message || 'Something went wrong');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="sm" sx={{
      minHeight: '100vh',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: 'linear-gradient(135deg, #003366 0%, #001a33 100%)'
    }}>
      <Paper elevation={3} sx={{
        p: 4,
        width: '100%',
        borderRadius: 2,
        background: '#fff',
        border: '2px solid #c19a49'
      }}>
        <Typography variant="h4" gutterBottom sx={{
          color: '#003366',
          textAlign: 'center',
          mb: 4,
          fontWeight: 'bold'
        }}>
          Sign Up
        </Typography>

        <form onSubmit={handleSubmit}>
          <TextField
            fullWidth
            label="Full Name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            sx={{ 
              mb: 3,
              '& .MuiOutlinedInput-root': {
                '&:hover fieldset': {
                  borderColor: '#c19a49',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#003366',
                }
              }
            }}
          />

          <TextField
            fullWidth
            label="Email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleChange}
            required
            sx={{ 
              mb: 3,
              '& .MuiOutlinedInput-root': {
                '&:hover fieldset': {
                  borderColor: '#c19a49',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#003366',
                }
              }
            }}
          />

          <TextField
            fullWidth
            label="Phone Number"
            name="phone"
            type="tel"
            value={formData.phone}
            onChange={handleChange}
            required
            sx={{ 
              mb: 4,
              '& .MuiOutlinedInput-root': {
                '&:hover fieldset': {
                  borderColor: '#c19a49',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#003366',
                }
              }
            }}
          />

          {error && (
            <Typography color="error" sx={{ mt: 2, textAlign: 'center' }}>
              {error}
            </Typography>
          )}
          
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, alignItems: 'center' }}>
            <Button
              type="submit"
              variant="contained"
              fullWidth
              disabled={loading}
              sx={{
                bgcolor: '#003366',
                '&:hover': {
                  bgcolor: '#004d99'
                }
              }}
            >
              {loading ? 'Signing up...' : 'Sign Up'}
            </Button>

            <Typography variant="body2" sx={{ mt: 2 }}>
              Already have an account?{' '}
              <Link
                component="button"
                variant="body2"
                onClick={() => navigate('/login', { state: { recordData } })}
                sx={{ color: '#c19a49' }}
              >
                Login
              </Link>
            </Typography>
          </Box>
        </form>
      </Paper>
    </Container>
  );
};

export default SignupPage;
