import axios from 'axios';

const BASE_URL = 'http://localhost:5001/api';

// Create axios instance
const api = axios.create({
  baseURL: BASE_URL,
  headers: {
    'Content-Type': 'application/json'
  }
});

// Add image base URL
export const IMAGE_BASE_URL = 'http://localhost:5001';

// Add token to requests if available
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Auth endpoints
export const auth = {
  sendOTP: (email) => api.post('/auth/send-otp', { email }),
  verifyOTP: (email, otp) => api.post('/auth/verify-otp', { email, otp }),
  signup: (userData) => api.post('/auth/signup', userData)
};

export const certificates = {
  getAll: () => api.get('/certificates'),
  getByRecordNo: (recordNo) => api.get(`/certificates/${recordNo}`),
  previewByRecordNo: (recordNo) => api.get(`/certificates/preview/${recordNo}`),
  create: (certificateData) => api.post('/certificates', certificateData),
  update: (recordNo, certificateData) => api.put(`/certificates/${recordNo}`, certificateData),
  delete: (recordNo) => api.delete(`/certificates/${recordNo}`)
};

export const sendContactMessage = async (data) => {
  return await axios.post(`${BASE_URL}/contact`, data);
};

export default api;
